.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.responsive-img {
  width: 100vw;
  height: auto;
  display: flex;
  justify-content: "center";
  margin: 0 auto;
  object-fit: contain;
  max-width: 90%;
  max-height: 90%;
}

.button-group {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
  margin-bottom: 80px;
  width: 30vw;
  max-width: 200px;
  height: auto
}

@media (orientation: landscape) {
  .responsive-img {
    width: auto;
    height: 50vh;
    max-width: 90%;
    max-height: 90%;
    margin: 0 auto;
    object-fit: contain;
    display: flex;
    justify-content: "center";
  }
  .button-group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3vw;
    margin-bottom: 4vh;
    max-width: 30vw;
    width: auto;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
